
import React, { Component } from 'react';
import './App.css';
import Tabletop from 'tabletop';

class App extends Component {
  constructor() {
    super()
    this.state = {
      data: []
    }
  }

  componentDidMount() {
    Tabletop.init({
      key: '1slegK9hBJWPAfEpyJbHTCSA-qAlkKxeRXc68MbOYs0Q',
      callback: googleData => {
        this.setState({
          data: googleData
        })
      },
      simpleSheet: true
    })
  }

  render() {
    console.log('updated state --->', this.state)
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">Эвенты-хуенты</h1>
        </header>
      </div>
    );
  }
}

export default App;